import { Component } from 'react';


export default class extends Component {

    static async getInitialProps({ query }) {
        const idChannel = query.id;
        const requestChannel = await fetch (`https://api.audioboom.com/channels/${ idChannel }`)
        const { body : { channel } } = await requestChannel.json(); 

        const requestAudio = await fetch (`https://api.audioboom.com/channels/${ idChannel }/audio_clips`)
        const { body : { audio_clips } } = await requestAudio.json();

        const requestChild = await fetch (`https://api.audioboom.com/channels/${ idChannel }/child_channels`)
        const { body : { channels } } = await requestChild.json();
        
        return { channel , audio_clips, channels };
    }

    render() {

        const { channel, audio_clips, channels } = this.props
        return (
            <div>
                <header>Podcasts</header>
                <h1>{ channel.title }</h1>
                { audio_clips.map( (clip) => (
                    <h2>{ clip.title }</h2>
                )) }

                { channels.map( (serie) => (
                    <h2>{ serie.title }</h2>
                )) }

                <style jsx>{`
                    header {
                        color: #fff;
                        background: #55E6C1;
                        padding: 15px;
                        text-align: center;
                        font-weight: bold;
                    }
                    .channels {
                        display: grid;
                        grid-gap: 15px;
                        padding: 15px;
                        grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
                    }
                    .channel img {
                        border-radius: 3px;
                        box-shadow: 0px 2px 6px rgba(0,0,0,0.15);
                        width: 100%;
                    }
                    h1 {
                        padding: 15px;
                        font-weight: 600;
                    }
                    h2 {
                        padding: 5px;
                        font-size: 0.9em;
                        font-weight: 600;
                        margin: 0;
                        text-align: absolute;
                    }
                `}</style>
                <style jsx global>{`
                    body {
                        margin: 0;
                        background: white;
                        font-family: system-ui;
                    }
                `}</style>
             </div> 
        )
    }
}


